.date-h {
    text-align: center;
}
.wrapper {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 40px 0px;
}
@media (max-width: 767px) {
    .wrapper {flex-direction: column;}
}
.cards {
    width: 160px;
    height: 80px;
    margin: 10px;
    border: 2px solid #fff;
    display: flex;
    align-items: center;
}

.text {
    margin: 0 auto;
}

.usd {
    color: rgb(31, 204, 31);
}

.eur {
    color: rgb(230, 67, 94);
}